import { AppbarContainer, AppShell } from "@app/ui";
import { Box } from "@mui/material";
import { useRouter } from "next/router";
import { useEffect, useState } from "react";

function Content({src}:{src:string}) {
    return (
        <Box sx={{ p: 2, color: "#333" }}>
           <video src={src} controls style={{ width: '100%' }} />
        </Box>
    )
}

export default function IntroPage() {
    const router = useRouter();
    
  const [src, setSrc] = useState('');

  useEffect(() => {
    if (router.isReady) {
      // 从 query 里拿
      const { src } = router.query;
      if (src) {
        const raw = Array.isArray(src) ? src[0] : src
        // 如果是 encodeURIComponent 传过来的，需要 decode
        setSrc(decodeURIComponent(raw));
      }
    }
  }, [router.isReady, router.query]);
    return (

        <AppShell>
            <AppbarContainer appbarProps={{ title: "播放器", back: true }}>
                {src && <Content src={src} />}
            </AppbarContainer>
        </AppShell>
    )
}
